@import 'variables.scss';
@import 'mixins/_grid.scss';
@import 'mixins/_helpers.scss';

@import 'type.scss';

section.container {
	article.title {
		h1 {
			float: left;
			@include font-size(100);
		}
		h4 {
			float: right;
			text-align: right;
			@include font-size(100);
			color: $secondaryTextColor;
		}
	}
	article.content {
		padding-top: 100px;
		padding-bottom: 100px;
		&>section, &>aside {
			border: None;
		}
		aside {
			text-align: right;
			a.button {
				@include font-size(20);
				font-weight: bold;	
			}
		}	
	}
}